/* Import Poppins font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root, .App {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column; /* Stack elements vertically */
  background-color: white;
  align-items: center;
}

body {
  background-color: #f0f0f0;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 20px;
  position: relative; /* To position logo absolutely within header */
}

.header img {
  position: absolute;
  top: 20px; /* Adjust to your preference */
  left: 20px; /* Adjust to your preference */
  width: 50px; /* Adjust to your preference */
  height: auto;
}

.header h1 {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(to bottom, rgb(246, 96, 121), rgb(246, 0, 123), #816694);
  -webkit-background-clip: text;
  color: transparent;
  margin: 0 auto; /* Center the h1 */
}

.header a {
  background: linear-gradient(to bottom, rgb(246, 96, 121), rgb(246, 0, 123), #816694);
  -webkit-background-clip: icon;
  color: white;
  height: 40px;
  width: 45px;
  border-radius: 50%;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0px; /* Adjust to your preference */
  font-size: 28px; /* Adjust to your preference */
  position: relative; /* Ensure the pseudo-element positions relative to the icon */
}



.line {
  width: 100%;
  height: 10px; /* Adjust height if needed */
  background: linear-gradient(to right, rgb(246, 96, 121), rgba(171, 46, 255, 0.679), #816694);
}

.page {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px 0 rgba(254, 88, 149, 0.9);
  width: 700px;
  height: 400px;
  background: linear-gradient(to right, #BB6D64 50%, white 50%);
  border-radius: 10px;
  overflow: hidden; /* Ensure child elements conform to border radius */
  margin-top: 100px; /* Space between line and content */
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 20px;
  text-align: center;
}

.box img {
  width: 200px;
  height: 200px;
  object-fit: cover;
}

input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #cccccc;
  border-radius: 5px;
}

h1 {
  font-family: 'Poppins', sans-serif;
  margin-bottom: 20px;
}

button {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  background-color: #816694;
  color: white;
  font-size: 16px;
}

.whiteBox {
  background: white;
}

.whiteBox h1 {
  background: linear-gradient(to bottom, rgb(246, 96, 121), rgb(255, 67, 161), #816694);
  -webkit-background-clip: text;
  color: transparent;
}

.pinkBox {
  font-family: 'Poppins', sans-serif;
  color: white;
  background: #BB6D64; /* Ensure the background color is maintained */
}

.pinkBox h1 {
  display: none;
}

.registerText {
  margin-top: 20px;
  font-size: 22px;
  font-weight: 600;
}
.pinkBox button{
  color: rgb(230, 116, 1);
  border-radius: 10px;
  height: 40px;
  width: 200px;
  background-color: white;
  text-decoration: none;
}

@media (max-width: 768px) {
  .header h1 {
    font-family: 'Poppins';
    font-size: 10px;
    margin-top: 35px;
    padding-right: 280px;
  }

  .header img {
    width: 30px;
    height: 30px;
  }

  .line {
    margin-top: 0px;
  }

  .line {
    width: 100%;
    height: 4px;
  }

  .box {
    width: 100%;
  }

  .page {
    margin-top: 50px;
    width: 100%;
    height: auto;
    flex-direction: column;
    box-shadow: none;
    background: none; /* Remove gradient for mobile view */
  }

  .pinkBox {
    width: 100%;
    height: 100px; /* Adjust height as needed */
    background-color: white;
    font-size: 14px; /* Adjust font size if needed */
  }

  .registerText {
    display: none;
  }

  .whiteBox h1 {
    display: none;
  }

  .pinkBox h1 {
    display: block;
    margin-top: 50px;
    font-family: 'Poppins';
    background: linear-gradient(to bottom, rgb(246, 96, 121), rgba(230, 1, 131, 0.799), #816694);
    -webkit-background-clip: text;
    color: transparent;
    font-size: 36px;
  }

  .pinkBox a {
    display: none;
  }

  .whiteBox {
    width: 100%;
    padding: 20px;
    box-shadow: none; /* Remove shadow for a cleaner look on mobile */
  }

  /* Optional: Adjust input fields and button for better spacing on mobile */
  input {
    padding: 12px;
    margin: 8px 0;
  }

  button {
    padding: 12px;
  }
}
